.hex {
  width: 20vmin;
  // width: calc(14vmin + 28px);
  height: 31vmin;
  // height: calc(22vmin + 44px);
  width: calc(16.8vmin + 29px);
  height: calc(26vmin + 45px);

  // Если надо уменьшить хексы, чтобы влезли боковушки

  // width: calc(15.5vmin + 29px);
  // height: calc(24vmin + 45px);

  // font-size: 10px;
  background: #FFF;
  position: absolute;
  // transform: translate(-50%, -50%);
  // transform: translateX(-50%) translateX(50%);
  border-radius: 10%;
  transition: left .4s,
    bottom .4s,
    right .4s,
    top .4s,
    transform .4s,
    visibility .4s,
    opacity .4s;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    // background: rgb(187, 187, 187);
    background: inherit;
    top: 0;
    left: 0;
    border-radius: inherit;
    z-index: -1;
  }

  &::before {
    transform: rotate(60deg);
  }

  &::after {
    transform: rotate(-60deg);
  }
}

.hexWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20% 0;
  height: 100%;
  width: 150%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.activeHex {

  .location {
    font-size: calc(2vmin + 10px);
    font-weight: 300;
  }

  .date {
    padding-top: 1.2vmin;
    font-size: calc(3vmin + 10px);
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
  }

  .time {
    font-size: calc(1.8vmin + 10px);
    font-weight: 300;
    padding-bottom: 1.2vmin;
  }

  button {
    background-color: inherit;
    padding: calc(1vmin + 3px) calc(1vmin + 3px);
    border: 1px solid black;
    border-radius: .5vmin;
    font-size: calc(1.2vmin + 5px);
    font-weight: 400;
  }
}

.passiveHex p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  // font-size: 5vmin;
  font-size: calc(3vmin + 20px);
  font-weight: 400;
}

.hexPosition {

  &0 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 0%;
    bottom: 19%;
  }

  &1 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 0%;
    bottom: 19%;
  }

  &2 {
    transform: translate(-50%, 50%) scale(0.36);
    left: 5%;
    bottom: 24%;
  }

  &3 {
    transform: translate(-50%, 50%) scale(0.6);
    left: 23%;
    bottom: 35%;
  }

  &4 {
    transform: translate(-50%, 50%) scale(1);
    left: 50%;
    bottom: 50%;
  }

  &5 {
    transform: translate(-50%, 50%) scale(0.6);
    left: 77%;
    bottom: 65%;
  }

  &6 {
    transform: translate(-50%, 50%) scale(0.36);
    left: 95%;
    bottom: 76%;
  }

  // left: 23%;
  //   bottom: 35%;
  //   left: 5%;
  //   bottom: 24%;

  &7 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 100%;
    bottom: 81%;
  }

  &8 {
    transform: translate(-50%, 50%) scale(0.2);
    left: 100%;
    bottom: 81%;
  }

  &0,
  &1,
  &7,
  &8 {
    visibility: hidden;
    opacity: 0;
  }
}

.isHidden {
  visibility: hidden;
  opacity: 0;
}

.sideBlock {

  &Left,
  &Right {
    // .hex
    // v v v v v v v v v v v v v v v
    // width: calc(16.8vmin + 29px);
    // height: calc(26vmin + 45px);
    // width: 45%;
    width: calc(48.5% - 30px);
    // width: calc(40% - 1vmin);
    // width: calc(44vmin - 30px);
    height: 15.5vmin;
    background: #fff;
    position: absolute;
    // width: calc(16.8vmin + 29px);
    height: calc((26vmin + 45px) / 2);

    &::before {
      display: block;
      content: '';
      position: absolute;
      transform: skew(30deg);
      height: 15.5vmin;
      height: calc((26vmin + 45px) / 2);
      background: inherit;
      right: 37vmin;
      width: 30vw;
    }
  }

  &Left {
    top: 50%;
    left: -10vmin;
    transform: translateY(-100%) skew(-30deg);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 5px;

    &::before {
      right: 37vmin;
    }
  }

  &Right {
    bottom: 50%;
    right: -10vmin;
    transform: translateY(100%) skew(-30deg);
    border-bottom-left-radius: 10px;
    border-top-left-radius: 5px;

    &::before {
      left: 37vmin;
    }
  }
}

.rivalName {

  &Left,
  &Right {
    position: absolute;
    transform: translateY(-50%) skew(30deg);
    top: 50%;
    left: 24%;
    font-size: 40px;
    font-size: calc(3vmin + 5px);
    font-weight: 600;
    background: purple;
    // width: calc(25vw + 5px);
    // width: 27vw;
  }

  &Left {
    text-align: right;
    right: 0;
    margin-right: 7vmin;
  }

  &Right {
    left: 0;
    margin-left: 7vmin;
  }
}
























