@media screen and (max-width: 100vh) {
  .sideBlock {

    &Left,
    &Right {
      width: calc(48vmin - 25px);
    }
  }


  .background {
    .circleInner {
      width: calc(25vmax + 39vmin);
      height: calc(25vmax + 39vmin);
    }

    .circleOuter {
      width: calc(40vmax + 60vmin);
      height: calc(40vmax + 60vmin);
    }
  }
}