.container {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}

a {
  text-decoration: none;
  color: #fff;
  padding: 20px 0;
  font-size: 25px;
  font-weight: 300;
  position: absolute;

  &:nth-child(1) {
    left: 0;
    margin-left: 2%;
  }

  &:nth-child(2) {
    left: 30%;
  }

  &:nth-child(3) {
    left: 50%;
    transform: translateX(-50%);
  }

  &:nth-child(4) {
    right: 28%;
  }

  &:nth-child(5) {
    right: 0%;
    margin-right: 2%;
  }
}